@media screen and (max-width: 767px) {

    .grid-container {
      display: inline-grid;
      grid-template-rows: auto;
    }
  
    .grid-header {
      grid-row: 1/2;
      background-color: aqua;
      background-image: linear-gradient(to right, #a1c4fd, #c2e9fb);
      text-align: center;
    }
  
    .grid-menu{
      grid-row: 2/3;
      background-color: grey;
      font-size:calc(10px + 1vw);
    }
  
    .grid-content{
      grid-row: 3/4;
      background-color: white;
      background-image: radial-gradient(ellipse at center, #ffffff 0%,#f6f6f6 47%,#ededed 100%);
      font-size:calc(12px + 1vw);
      margin-top: 10px;
    }
  
    .grid-widget{
      grid-row: 4/5;
      background-color: #F6D155;
      background-image: linear-gradient(to right, #fcb69f, #ffecd2);
      padding: 10px;
    }
  
    footer{
      background-color: lightgreen;
      background-image: linear-gradient(to bottom, #b4e391 0%,#61c419 50%,#b4e391 100%);
      padding: 10px;
    }
  
    .navmenu {
        padding: 0;
        margin: 0;
        background-image: linear-gradient(to bottom, #4c4c4c 0%,#595959 12%,#666666 25%,#474747 39%,#2c2c2c 50%,#000000 51%,#111111 60%,#2b2b2b 76%,#1c1c1c 91%,#131313 100%);
        background-color: black;
        float: left;
    }
  
    .navmenu a {
        text-decoration: none;
        color: white;
    }
  
    .navmenu li {
        float: left;
        display: inline;
        position: relative;
        min-width: 225px;
        min-height: 40px;
        list-style: none;
    }
  
    .navmenu ul {
        position: absolute;
        padding: 0;
        top: 100%;
        display: none;
    }
  
    .navmenu ul li {
        background-image: radial-gradient(ellipse at center, #e4f5fc 0%,#bfe8f9 50%,#9fd8ef 51%,#2ab0ed 100%);
        background-color: white;
    }
  
    .navmenu ul a {
        color: black;
    }
  
    .navmenu ul ul {
        padding: 0;
        left: 100%;
        top: 0;
        background: white;
    }
  
    .navmenu li:hover > ul {
        display: block;
    }
  
    .navmenu li:hover {
        background: radial-gradient(ellipse at center, #ffc578 0%,#fb9d23 100%);
    }
  
}  